@charset "utf-8";

////colors////

$pink01: #fdf6f1;
$pink02: #d55d6b;
$pink03: #F7C9D6;

$green01:#43868e;

$gray01: #DCDCDC;


////font////
@mixin poppins {
  font-family: 'Poppins', sans-serif;
}

////wight////
$regular: 400;
$medium: 500;
$bold: 700;

/* =========================================================
header
========================================================= */
.header {
  &__title {
    text-align: center;
    img {
      vertical-align: bottom;
    }
  }
}
@media screen and (min-width:769px) {
  .header {
    &__title {
      img {
        width: 100%;
        // width: auto;
        // max-width: 100%;
      }
    }
  }
}


/* =========================================================
Main
========================================================= */
.main-contents {
  overflow: hidden;
  background-color: #FDF6F1;
  padding-bottom: 100px;
  margin-bottom: -70px;
  @media screen and (min-width: 769px) {
    padding: 0 20px 100px;
  }
  @media screen and (max-width: 768px) {
    padding-bottom: 118px;
    margin-bottom: -52px;
  }
}


/* =========================================================
Local navi
========================================================= */
.localnav {
  max-width: 1200px;
  margin: 30px auto 0;
  padding: 0 20px;
  @media screen and (min-width: 769px) {
    padding: 0;
  }
  &-list {
    display: flex;
    flex-wrap: wrap;
  }
  &-item {
    width: calc(50% - 5px);
    margin: 10px 10px 0 0;
    &:nth-child(-n+2) {
      margin-top: 0;
    }
    &:nth-child(2n) {
      margin-right: 0;
    }
    a,
    button {
      width: 100%;
      height: 40px;
      font-size: 13px;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 1px solid #000;
      color: #000;
      text-decoration: none;
    }
  }
}


/* =========================================================
New&Renew items
========================================================= */
[v-cloak] {
  display: none;
}

.newrenew-block {
  max-width: 1024px;
  margin: 0 auto;
  padding: 100px 20px 0;
  @media screen and (max-width: 768px) {
    padding: 80px 20px 0;
  }
  @media (min-width: 769px) {
    // padding: 0;
  }
  .list-block {
    margin: 0 auto 50px;
    @media (min-width: 769px) {
      margin: 0 auto 85px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  .list-ttl {
    height: 7.4vw;
    max-height: 68px;
    margin: 0 auto 70px;
    text-align: center;
    @media screen and (max-width: 768px) {
      height: 13.6vw;
      max-height: 51px;
    }
    img {
      width: auto;
      height: 100%;
    }
  }
}

[class*="item-list"] {
  .item {
    background: #fff;
    position: relative;
  }
  .badge-l {
    background: $pink02;
    @include poppins;
    color: #fff;
    position: absolute;
  }
  .badge-r {
    background: url(../images/bg_badge.png) no-repeat center;
    background-size: contain;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    .txt {
      color: #fff;
    }
  }
  .open-event {
    .ttl {
      color: $pink02;
      font-weight: $bold;
    }
  }
  .link-list {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .link-item {
    a {
      border-radius: 50%;
      background: $green01;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      transition: background .2s ease;
      &:hover {
        @media (min-width: 769px) {
          background: $pink02;
        }
      }
    }
  }
}

.item-list01 {
  .item {
    margin: 0 auto 80px;
    @media (min-width: 769px) {
      margin: 0 auto 100px;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  .badge-l {
    padding: 18px 30px 18px 25px;
    border-radius: 0 40px 40px 0;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    top: -30px;
