        font-size: 20px;
      }
      @media (min-width: 769px) {
        font-size: 28px;
      }
    }
  }
  .badge-r {
    width: 78px;
    height: 78px;
    top: 10px;
    right: 10px;
    &.type2 {
      background-image: url(../images/bg_badge02.png);
    }
    &.type3 {
      background-image: url(../images/bg_badge03.png);
    }
    &.type4 {
      background-image: url(../images/bg_badge04.png);
    }
    &.type5 {
      background-image: url(../images/bg_badge05.png);
    }
    &.type6 {
      background-image: url(../images/bg_badge06.png);
    }
    @media (min-width: 769px) {
      width: 114px;
      height: 114px;
      top: -20px;
      right: -20px;
    }
