      font-size: 16px;
      line-height: 1;
      font-weight: $bold;
    }
  }
  .detail {
    padding: 0 30px;
    @media (min-width: 769px) {
      margin: 10px auto 0;
      padding: 0 70px;
      width: 390px;
      // display: flex;
      // justify-content: space-between;
    }
  }
  .shop-info {
    padding: 0 0 20px;
    border-bottom: 1px solid $gray01;
    @media (min-width: 769px) {
      // width: calc(50% - 20px);
      border-bottom: none;
    }
  }
  .logo {
    height: 106px;
    margin: 0 auto 15px;
    border-bottom: 1px solid $gray01;
    text-align: center;
    img {
      width: auto;
      height: 100%;
    }
  }
  .shop {
    font-size: 16px;
    line-height: 1.56;
    text-align: center;
  }
  .floor {
    margin: 10px auto 0;
    font-size: 14px;
    line-height: 1;
    font-weight: $regular;
    text-align: center;
  }
  .ctg {
    margin: 5px auto 0;
    font-size: 14px;
    line-height: 1.78;
    font-weight: $regular;
    text-align: center;
  }
  .txt-wrap {
    margin: 20px auto 0;
    font-size: 14px;
    line-height: 1.78;
    @media (min-width: 769px) {
      margin: 0;
      padding: 30px 0 0;
    }
  }
  .open-event {
    margin: 20px auto 0;
    .ttl {
      margin: 0 auto 1em;
      font-size: 16px;
      font-weight: $bold;
    }
  }
  .link-list {
    margin: 35px auto 0;
    @media (min-width: 769px) {
      margin: 50px auto 0;
    }
  }
  .link-item {
    margin: 0 15px 0 0;
    &:last-child {
      margin-right: 0;
    }
    a {
      width: 42px;
      height: 42px;
    }
  }
  .more {
    margin-top: 35px;
    width: 100%;
    @media (min-width: 769px) {
      margin-top: 30px;
    }
    button {
      width: 100%;
      height: 30px;
      background: $green01;
      @include poppins;
      color: #fff;
      font-size: 14px;
      line-height: 1;
      letter-spacing: .2em;
      display: flex;
      align-items: center;
      justify-content: center;
      @media (min-width: 769px) {
        height: 50px;
        font-size: 20px;
      }
    }
    }
}

.item-list02 {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -23px;
