    box-sizing: border-box;
    background-color: transparent;
    margin-top: 55px;
    @media (min-width: 769px) {
      width: 33.33%;
      margin-top: 70px;
      padding: 10px 23px 0;
      &:nth-child(1),
      &:nth-child(2),
      &:nth-child(3) {
