.newrenew-modal {
  .modal-wrap {
    width: calc(100vw - 40px);
    max-width: 900px;
    margin: 0 auto;
    position: relative;
    @media (min-width: 769px) {
      width: calc(100vw - 140px);
      max-width: 1024px;
      box-sizing: border-box;
    }
  }
  .modal-cont {
    padding: 0 0 40px;
    background: #fff;
    box-sizing: border-box;
    @media (min-width: 769px) {
      height: calc(100vh - 240px);
      max-height: 680px;
      padding: 50px 50px 30px;
      overflow: auto;
    }
  }
  .badge-wrap {
    @media (min-width: 769px) {
      display: flex;
      position: absolute;
      top: -20px;
      left: -20px;
    }
  }
  .badge-l {
    padding: 18px 30px 18px 25px;
    border-radius: 0 40px 40px 0;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    top: -30px;
    left: -20px;
    z-index: 1;
    @media (min-width: 769px) {
      position: relative;
      top: auto;
      left: auto;
    }
    > * {
      margin-right: .4em;
      color: #fff;
      line-height: 1;
      font-weight: $medium;
      &:last-child {
        margin-right: 0;
      }
    }
    .date {
      font-size: 24px;
      @media (max-width: 360px) {
        font-size: 20px;
      }
      @media (min-width: 769px) {
        font-size: 34px;
      }
    }
    .weeks {
      font-size: 18px;
      letter-spacing: .04em;
      @media (max-width: 360px) {
        font-size: 16px;
      }
      @media (min-width: 769px) {
        font-size: 22px;
      }
    }
    .new-renew {
      font-size: 24px;
      letter-spacing: .04em;
      @media (max-width: 360px) {
        font-size: 20px;
      }
      @media (min-width: 769px) {
        font-size: 28px;
      }
    }
  }
  .badge-r {
    width: 78px;
    height: 78px;
    top: -30px;
    right: -10px;
    z-index: 1;
    @media (min-width: 769px) {
      width: 88px;
      height: 88px;
      top: auto;
      right: auto;
    }
    .txt {
      font-size: 16px;
      line-height: 1;
      font-weight: $bold;
    }
  }
  .cont-wrap {
    @media screen and (min-width: 769px) {
      display: flex;
      justify-content: space-between;
    }
  }
  .img-wrap {
    position: relative;
    @media screen and (min-width: 769px) {
      width: 500px;
      margin-right: 30px;
    }
  }
  .img-slider {
    position: relative;
    .slick-arrow {
      position: absolute;
      top: calc(50% - 15px);
      transform: translateY(-50%);
      z-index: 1;
      &.prev {
        left: 5px;
      }
      &.next {
        right: 5px;
      }
      button {
        display: block;
        width: 35px;
        height: 35px;
        background: #fff;
        border: 1px solid $pink02;
        border-radius: 50%;
        &:before {
          content: "";
          display: block;
          width: 10px;
          height: 10px;
          border-top: 1px solid $pink02;
          border-left: 1px solid $pink02;
          position: absolute;
          top: 12px;
          left: 14px;
          transform: rotate(-45deg);
        }
        &.slick-next {
          transform: scale(-1,-1);
        }
      }
    }
    .slick-dots {
      margin: 15px auto 0;
      display: flex;
      align-items: center;
      justify-content: center;
      li {
        margin: 0 15px 0 0;
        &:last-child {
          margin: 0;
        }
      }
      button {
        width: 15px;
        height: 15px;
        background: $pink02;
        border-radius: 50%;
        text-indent: 100%;
        white-space: nowrap;
        overflow: hidden;
      }
      .slick-active {
        button {
          background: $pink03;
        }
      }
    }
  }
  .detail {
    margin: 40px auto 0;
    padding: 0 30px;
    box-sizing: border-box;
    @media screen and (min-width: 769px) {
      width: calc(100% - 500px - 30px);
      margin: 0;
      padding: 0;
    }
  }
  .dateWrap {
    display: flex;
    justify-content: center;
    &-date {
      font-size: 14px;
      color: #fff;
      padding: 4px 8px 3px;
      background-color: #D55D6B;
    }
    &-first {
      margin-left: 10px;
      padding: 4px 8px 3px;
      border: solid 1px #43868E;
      color: #43868E;
      font-size: 14px;
    }
  }
  .logo {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 205px;
    height: 106px;
    margin: 15px auto;
    border-bottom: none;
    img {
      width: auto;
      height: 100%;
      flex-shrink: 0;
    }
  }
  .shop {
    padding: 15px 0 0;
    font-size: 16px;
    line-height: 1.56;
    text-align: center;
  }
  .floor {
    margin: 15px auto 0;
    font-size: 14px;
    font-weight: $regular;
    text-align: center;
  }
  .ctg {
    margin: 10px auto 0;
    font-size: 14px;
    line-height: 1.78;
    font-weight: $regular;
    text-align: center;
  }
  .introduction {
    margin: 20px auto 0;
    padding: 20px 0 0;
    border-top: 1px solid $gray01;
    font-size: 14px;
    line-height: 1.78;
  }
  .open-event {
    margin: 25px auto 0;
    .ttl {
      font-size: 16px;
      font-weight: $bold;
      text-align: center;
      @media screen and (max-width: 768px) {
        text-align: left;
      }
    }
    .txt {
      margin: 1em auto 0;
      font-size: 14px;
      line-height: 1.78;
    }
  }
  .link-list {
    margin: 20px auto 0;
    display: flex;
    justify-content: center;
    align-items: center;
    @media (min-width: 769px) {
      margin: 40px auto 0;
    }
  }
  .link-item {
    margin: 0 15px 0 0;
    &:last-child {
      margin-right: 0;
    }
    a {
      width: 42px;
      display: block;
    }
  }
  .modal-arrow-wrap {
    margin: 20px auto 0;
    position: relative;

  }
  .modal-arrow-close {
    margin: 20px auto 0;
    text-align: center;
    @media (min-width: 769px) {
      margin: 27px auto 0;
    }
    button {
      font-size: 20px;
      line-height: 1;
      letter-spacing: .2em;
      color: $green01;
    }
  }
  .modal-arrow {
    position: absolute;
    top: calc(100% - 28px);
    @media (min-width: 769px) {
      top: calc(50% - 20px);
      transform: translateY(-50%);
    }
    button {
      width: 35px;
      height: 35px;
      display: block;
      border-radius: 50%;
      background: $green01;
      position: relative;
      font-size: 0;
      @media (min-width: 769px) {
        width: 50px;
        height: 50px;
      }
      &:before {
        content: "";
        width: 10px;
        height: 10px;
        border-top: 2px solid #fff;
        border-left: 2px solid #fff;
        position: absolute;
        top: 12px;
        left: 13px;
        transform: rotate(-45deg);
        @media (min-width: 769px) {
          width: 15px;
          height: 15px;
          top: 17px;
          left: 20px;
        }
      }
    }
    &.prev {
      left: 0;
      @media (min-width: 769px) {
        left: -70px;
      }
    }
    &.next {
      right: 0;
      @media (min-width: 769px) {
        right: -70px;
      }
      button {
        transform: scale(-1,-1);
      }
    }
  }
}




/* =========================================================
modal
========================================================= */
.modal-block {
  width: 100vw;
  height: 100vh;
  padding: 110px 0 100px;
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  overflow: scroll;
  z-index: 50;
  display: none;
  @media (min-width: 769px) {
    padding: 125px 0 0;
    overflow: auto;
  }
}
.modal-bg {
  width: 100vw;
  height: 100%;
  background: rgba(253, 246, 241, .95);
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
}
.modal-close {
  width: 32px;
  height: 32px;
  margin: 0 0 0 auto;
  position: absolute;
  top: -57px;
  right: 0;
